// 背景图
.bg-normal(@img, @ext: 'png', @size: 100% 100%, @position: center, @repeat: no-repeat) {
    background: @position ~'/' @size @repeat url('@{img}');
}

body {
    position: relative;
    height: 100vh;
    overflow: hidden;
    // background: linear-gradient(to bottom, transparent 500px, #92511e) !important;
}

@keyframes identifier2 {
    form {
        transform: translateY(900px) translateZ(0);
    }
    to {
        transform: translateY(0px) translateZ(0);
    }
}

@keyframes shipin {
    0% {
        opacity: 0;
    }

    40% {
        opacity: 1;
        transform: scale3d(1, 1, 1);
    }

    100% {
        transform: translate3d(-500px, -700px, 0) scale3d(0.5, 0.5, 0.5);
    }
}

#root {
    // position: absolute;
    height: 100vh;
    width: 100%;
    transform: translateY(900px) translateZ(0);
    // left: 0;
    // top: 0;
    animation: identifier2 0.45s ease-out forwards;
    // animation: showpage 1s ease-in forwards;
    // top: constant(safe-area-inset-bottom); /* iOS 11.0 */
    // top: env(safe-area-inset-bottom); /* iOS 11.0 */
}

::-webkit-scrollbar { // 直接复制黏贴到样式页.css，完美解决
    display: none;

    /* background-color:transparent; */
}

.m-effect-wrap {
    width: 375px;
    height: 335.33px;
    position: absolute;
    bottom: 20px;
    z-index: 99;

    img.u-effect-mask {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
    }

    .banner {
        display: block;
        height: 100%;
        width: 100%;
        position: absolute;
        bottom: 0 !important;
        top: unset;
        z-index: 99;
        overflow: hidden;
    }
}

.m-main {
    height: 100vh;
    .bg-normal('./img/bg.png');
    color: white;
    overflow: hidden;
    position: relative;

    // 倒计时样式
    &-countdown {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 110px;
        height: 38px;

        &-desc {
            font-weight: bold;
            font-size: 16px;
            color: #90afd8;
            line-height: 18px;
        }

        &-second {
            font-weight: bold;
            font-size: 18px;
            color: #90afd8;
            line-height: 18px;
        }

        &-left {
            width: 6px;
            height: 38px;
            margin-right: 5px;
        }

        &-num {
            height: 38px;
            line-height: 38px;
            display: flex;
            flex-direction: row;
            align-items: center;

            &--margin {
                margin-left: 5px;
                margin-right: 2px;
            }

            &-small {
                height: 20px;
                width: 14px;
            }
        }

        &-right {
            width: 6px;
            height: 38px;
            margin-left: 5px;
        }
    }

    &-howplay {
        width: 54px;
        height: 25px;
        position: absolute;
        left: 0px;
        top: 120px;
        font-size: 0;
        background: url(./img/howplay.png) no-repeat center/cover;
    }

    // 问号按钮
    &-rank {
        width: 54px;
        height: 25px;
        .bg-normal('./img/rank.png');
        position: absolute;
        left: 0px;
        top: 80px;
        z-index: 2;
    }

    // 问号按钮
    &-record {
        width: 55px;
        height: 28px;
        .bg-normal('./img/record.png');
        position: absolute;
        right: 0px;
        top: 80px;
        z-index: 2;
    }

    // 温馨提示
    &-notify {
        margin-top: 8px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        &-icon {
            width: 11px;
            height: 9px;
        }
    }

    &-planets {
        width: 428px;
        height: 300px;
        // background: grey;
        position: relative;

        &-wrapper {
            width: 100%;
            height: 300px;
            position: absolute;
            // background: green;
            overflow-x: scroll;
            // -webkit-overflow-scrolling: touch;
            overflow-y: hidden;
            top: 170px;
            left: 0;
            right: 0;
        }
    }

    // 弹窗
    &-ship-modal .mrc-modal-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    &-bottom {
        display: flex;
        // transform: scale(1.1, 1.1);
        flex-direction: row;
        align-items: flex-end;
        justify-content: center;
        font-weight: 600;
        padding-bottom: 10px;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%) scale(1.1, 1.1);

        &-left {
            box-sizing: border-box;
            width: 147px;
            height: 37px;
            background-image: url('~$base/imgs/left.png');
            background-size: 100% 100%;
            font-size: 12px;
            line-height: 12px;
            padding: 12px 28px 0 72px;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis; //溢出用省略号显示
            white-space: nowrap; //溢出不换行
        }

        &-get {
            margin-left: -3px;
            width: 68px;
            height: 37px;
            background-image: url('~$base/imgs/get.png');
            background-size: 100% 100%;
            font-size: 9px;
            line-height: 31px;
        }

        &-send {
            margin-left: 7px;
            box-sizing: border-box;
            width: 105px;
            height: 59px;
            background-image: url('~$base/imgs/pq.png');
            background-size: 100% 100%;
            font-size: 13px;
            line-height: 13px;
            padding: 30px 22px 0 54px;
            text-align: center;

            &--disable {
                background-image: url('~$base/imgs/pq_disable.png');
            }
        }
    }

    &-loading {
        &-ship {
            z-index: 4;
            position: absolute;
            top: 450px;
            left: 270px;
            width: 310px;
            height: 280px;
            background-image: url('~$base/imgs/fc.png');
            background-size: 100% 100%;
            // opacity: 0;
            animation: shipin 4s ease-in 1;
            animation-fill-mode: forwards;
        }
    }
}
