.m-getship {
    background: #112f3b;
    color: white;
    width: 100vw;

    &-loading {
        min-height: 200px;
        background: #112f3b;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    &-header {
        padding: 27px 18px 18px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        &-icon {
            width: 54px;
            height: 54px;
            background-size: 100% 100%;
            margin-right: 18px;
            flex-shrink: 0;
        }

        &-desc {
            flex: 1;
            font-size: 12px;

            &-title {
                font-size: 15px;
            }
        }
    }

    &-selects {
        margin-top: 10px;
        padding: 0 18px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        font-size: 12px;
    }

    &-select {
        flex-grow: 0;
        width: 78px;
        height: 27px;
        background: #129ace;
        box-shadow: 0 0 30px 0 #117da6;
        border-radius: 14px;
        line-height: 27px;
        text-align: center;

        &--disable {
            background: rgba(255, 255, 255, 0.2);
            box-shadow: none;
        }
    }

    &-count {
        margin-top: 6px;
        padding: 0 18px;
        font-size: 11px;
        color: rgba(255, 255, 255, 0.6);
    }

    &-footer {
        background: rgba(255, 255, 255, 0.1);
        margin-top: 20px;
        padding: 0 20px;
        height: 60px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        font-size: 15px;

        &-count {
            color: rgba(248, 212, 78, 0.8);
        }

        &-button {
            width: 80px;
            height: 30px;
            line-height: 30px;
            background: #129ace;
            border-radius: 40px;
            text-align: center;
        }
    }
}
