.m-announce {
    position: absolute;
    top: 84px;
    left: 70px;
    width: 235px;
    height: 20px;
    border: 1px solid rgba(173, 205, 254, 0.6);
    border-radius: 10px;
    padding: 0 6px;
    line-height: 20px;
    text-align: center;
    font-size: 9px;
    color: #b0e4ff;
    box-sizing: border-box;

    .m-notify  {
        height: 100%;

        .m-notify-item {
            height: 20px;
            line-height: 20px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
}