.m-sendship {
    width: 375px;
    height: 331px;
    background-image: url('~$base/imgs/send_bg.png');
    background-size: 100% 100%;
    background-color: none;
    overflow: hidden;

    &-content {
        margin-top: 37px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: flex-start;
    }

    &-planet {
        margin-left: 6px;
        width: 131px;
        height: 131px;
        background-size: 100% 100%;
        position: relative;

        &-num {
            font-weight: bold;
            position: absolute;
            top: 73px;
            left: 72px;
            font-size: 9px;
            color: #96deff;
        }
    }

    &-send {
        margin-left: 5px;
        margin-right: 32px;
        flex: 1;

        &-num {
            color: #fff;
            line-height: 14px;
            font-size: 14px;
            margin-top: 29px;
        }

        &-operate {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            margin: 20px -20px 0;

            &-hotarea {
                padding: 20px;
            }

            &-minus {
                width: 26px;
                height: 26px;
                background-image: url('~$base/imgs/jian.png');
                background-size: 100% 100%;

                &--disable {
                    background-image: url('~$base/imgs/jian_disable.png');
                }
            }

            &-number {
                flex: 1;
                text-align: center;
                font-size: 33px;
                color: #fff;
                font-weight: 800;
            }

            &-add {
                width: 26px;
                height: 26px;
                background-image: url('~$base/imgs/jia.png');
                background-size: 100% 100%;

                &--disable {
                    background-image: url('~$base/imgs/jia_disable.png');
                }
            }
        }
    }

    &-confirm {
        position: absolute;
        top: 31px;
        right: 16px;
        width: 73px;
        height: 26px;
        background-image: url('~$base/imgs/qr.png');
        background-size: 100% 100%;
        z-index: 10;
    }

    &-select {
        margin-top: 31px;
        margin-left: 40px;
        font-size: 14px;
        color: #fff;
    }

    &-steps {
        margin: 20px 33px 20px 40px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

    &-step {
        color: #fff;
        font-size: 14px;
        background-image: url('~$base/imgs/num2.png');
        background-size: 100% 100%;
        width: 74px;
        height: 27px;
        line-height: 27px;
        text-align: center;

        &--disable {
            background-image: url('~$base/imgs/num1.png');
        }
    }
}
