.modal-wrap {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 999;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);

    @keyframes bottomshow {
        from {
            bottom: -100vh
        };
        to {
            bottom: 0
        }
    }

    @keyframes fadeinshow {
        from {
            opacity: 0;
        };
        to {
            opacity: 1;
        }
    }

    .modal-content {
        width: fit-content;
        height: auto;
        margin: 0 auto;
        font-size: 14px;

        &.center {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }

        &.bottom {
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translate(-50%);
            animation: bottomshow 0.3s ease-out forwards;
        }

        &.top {
            position: absolute;
            left: 50%;
            top: 0;
            transform: translate(-50%);
        }

        &.fadein {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            animation: fadeinshow 0.3s ease-out forwards;
        }
    }
}