.m-planet {
    position: absolute;
    z-index: 2;

    &-total {
        font-weight: bold;
        box-sizing: border-box;
        position: absolute;

        &-textwrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            font-size: 10px;
            line-height: 10px;
        }
    }

    &-times {
        position: absolute;
        font-size: 9px;
        font-weight: bold;
        color: #96deff;
        line-height: 9px;
    }

    &-button {
        background-image: url('~$base/imgs/selectnum_bg.png');
        background-size: 100% 100%;
        position: absolute;
        box-sizing: border-box;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        font-size: 10px;
        line-height: 10px;
        padding-right: 2px;

        &-textwrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            flex: 1;
            margin-right: -3px;
        }

        &-num {
            text-align: center;
            line-height: 10px;
        }

        &-unit {
            margin-left: 1px;
        }

        &-add {
            display: inline-block;
            width: 20px;
            font-size: 10px;
            line-height: 10px;
            position: relative;
            top: -2px;
            text-align: center;
        }
    }

    &-hotarea {
        z-index: 3;
        position: absolute;
    }
}

